/*
--- Color Theme ---
Dark orange: #E27D60
Light green: #84cdca
Peach: #E8A87C
Light purple: #C38D9E
Dark green: #41B3A3
*/

.App {
  text-align: center;
}

.App-header {
  background-color: #84cdca;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

#text {
  font-family: FuturaBold;
  color: #f5f0f0;
}

.title {
  margin: 0;
}

.description {
  margin: 0;
}

.logo {
  height: 60px;
  margin: 20px;
}

.profile {
  border-radius: 50%;
  width: 20%;
  border: 5px solid #C38D9E;
  margin-top: 10px;
}

.aboutText {
  color: #f5f0f0;
  margin: 0 20% 3% 20%;
}